* {
    margin: 0;
    padding: 0;
}

#root {
    min-height: 100vh;
    background-color: #202632;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Manrope', sans-serif;
}

.loading {
    position: fixed;
    display: grid;
    place-content: center;
    height: 100vh;
    width: 100vw;
    background-color: #202632;
    z-index: 99;
}

.card {
    background-color: #313a49;
    max-width: 450px;
    padding: 4rem 4rem 5rem 4rem;
    border-radius: .75rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
    box-shadow: 0 0 16px 17px #14181e40;
}

.card .opacity .categorie {
    color: #4fe99e;
    font-size: .7rem;
    text-transform: uppercase;
    font-weight: 800;
    letter-spacing: 5px;
}

.card .opacity q {
    color: #cbe0e7;
    font-weight: 800;
    font-size: 1.5rem;
    margin-top: 1.25rem;
    margin-bottom: 1.563rem;
    letter-spacing: 1px;
    line-height: 35px;
}

.card .opacity .author {
    position: relative;
    width: 100%;
}

.card .opacity .author::after {
    content: '';
    position: absolute;
    top: 55%;
    left: 0;
    background-color: #535c6b;
    width: 100%;
    height: 2px;
}

.card .opacity .author p {
    color: #889ca3;
    font-weight: 800;
    font-size: .75rem;
    letter-spacing: 1px;
    background-color: #313a49;
    display: inline-block;
    position: relative;
    padding: 0 0.7rem;
    z-index: 9;
}

.card button {
    border: none;
    outline: none;
    position: absolute;
    bottom: -30px;
    padding: 1rem;
    background-color: #52feaa;
    border-radius: 50%;
    cursor: pointer;
    transition: .3s;
    display: grid;
    place-content: center;
}
.card button:hover {
    box-shadow: 0 0 17px 14px #52feaa45;
    filter: brightness(1.2);
}

@media (max-width: 720px) {
    .card {
        max-width: 75%;
        padding: 3rem 2.2rem 3.5rem 2.2rem;
        border-radius: 0.65rem;
    }
    .card .categorie {
        font-size: .6rem;
    }
    .card q {
        font-size: 1.25rem;
        margin-bottom: 1.4rem;
        line-height: 30px;
    }
}

.opacity {
    display: grid;
    opacity: 0;
    transition: .5s;
    user-select: none;
}